@use "../abstracts";

.header {
    position: relative;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;

    &::before {
        content: "";
        background-color: rgba(0, 0, 0, 0.35);
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
    }

    .heading-primary {
        line-height: 1;
    }

    .layer {
        position: absolute;
        width: 100%;
        height: 100vh;
        background-position: center;
        background-size: cover;
        object-fit: cover;
    }

    .content {
        z-index: 9;
        text-align: center;

        .job-title {
            font-size: 2rem;
            color: #fff;
            text-transform: uppercase;
            font-weight: lighter;
            letter-spacing: 5px;
        }
    }

    &__bg {
        background-image: url("../img/hero-1.jpg");
        z-index: 1;
    }

    // &__rock{
    //     z-index: 3;
    //     background-image: url("../img/rock.png");
    // }

    &__bottom-fade {
        height: 15vh;
        width: 100%;
        bottom: 0;
        z-index: 4;
        position: absolute;
        background: linear-gradient(
            to top,
            black 0%,
            rgba(0, 0, 0, 0.738) 19%,
            rgba(0, 0, 0, 0.541) 34%,
            rgba(0, 0, 0, 0.382) 47%,
            rgba(0, 0, 0, 0.278) 56.5%,
            rgba(0, 0, 0, 0.194) 65%,
            rgba(0, 0, 0, 0.126) 73%,
            rgba(0, 0, 0, 0.075) 80.2%,
            rgba(0, 0, 0, 0.042) 86.1%,
            rgba(0, 0, 0, 0.021) 91%,
            rgba(0, 0, 0, 0.008) 95.2%,
            rgba(0, 0, 0, 0.002) 98.2%,
            transparent 100%
        );
    }
}
